$bg: #141e30ff;
$secondary: #04293aff;
$secondary2: #243b55ff;
$tertiary: #A24936;
$highlight: #D36135;
$blue: #494ca6;
$lightblue: #a7c0fa;
$text: #DCD7C9;
$waveheight: min(30vh, 40vw);
$redlines: false;
$vh: calc(#{var(--height, 100vh)}/100);
$vw: calc(#{var(--width, 100vw)}/100);

:root {
  --bg: #{$bg};
  --secondary: #{$secondary};
  --secondary2: #{$secondary2};
  --tertiary: #{$tertiary};
  --highlight: #{$highlight};
}

* {
  @if $redlines {
    outline: red solid 1px;
  }
}

html {
  // overflow: auto;
  min-height: 100%;
}

#mobile {
  display: inline-block;
}
#desktop {
  display: none;
}

@media only screen and (min-width: 768px){
  #mobile {
    display: none;
  }
  #desktop {
    display: inline-block;
  }
}

.App {
  font-family: sans-serif;
  background-color: $bg;
  height: 100%;
  width: 100%
}

#root {
  height: 100%;
}

body {
  background-color: $bg;
  height: 100%;
  width: 100%;
  margin: 0;
  // transform-style: preserve-3d;
  // overflow-y: auto;
  overflow-x: hidden;
  // perspective: calc($parallaxdegree * 1px);
}

div {
  background-color: $secondary;
  border-radius: 10px;
  color: $text;

}

p, a {
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  font-size: 18pt;
  margin: 0px;
  padding: 10px;
  color: $text;
}

h1 {
  font-family: "Ubuntu", sans-serif;
  font-size: 50pt;
  color: $text
}

#title {
  position: fixed;
  width: calc(90 * $vw);
  height: calc(40 * $vh);
  left: 0;
  top: 0;
  backdrop-filter: blur(8px);
  background-color: rgba($bg, 0.5);
  margin: calc(5 * $vw);

  span {
    display: inline-block;
    vertical-align: middle;
    line-height: normal;
    padding: 7%;

    h1 {
      color: $highlight;
      margin-top: 10px;
      text-align: center;
    }
  }

}

.links {
  position: fixed;
  top: 50%;
  bottom: calc(-5 * $vh);
  left: 0;
  right: 50%;
  margin: 5%;
  padding: 5%;
  backdrop-filter: blur(8px);
  background-color: rgba($bg, 0.5);
  text-align: center;
  display: table;

  a {
    display: table-row;

    font-size: calc(28.6vh/3); 
  }
  a:link { text-decoration: none; }
  a:visited { text-decoration: none; }
  a:hover { text-decoration: none; }
  a:active { text-decoration: none; }
}

#titlebuttons {
  position: fixed;
  left: 50%;
  right: 0;
  top: 50%;
  bottom: calc(5 * $vh);
  margin: 5%;
  background: none;

  button {
    background: $highlight;
    border: none;
    color: $text;
    font-size: min(8vw, 5vh);
    width: 100%;
    padding: min(10%, 3vh);
    transform: skewX(-10deg);
    margin-bottom: calc(10 * $vh);
    transition-duration: 0.5s;
    div {
      background: none;
      transform: skewX(10deg);
    }
  }
  button:hover {
    background: $text;
    div {
      color: $highlight;
    }
  }
}

.desktop {
  display: none;
}

@media only screen and (orientation: landscape) {
  .links {
    padding: 2% 5%;
    a {
      display: table-cell;
    }
  }
  #title {
    span {
      padding-top: 2%;
      font-size: 5vh;
    }
    h1 {
      font-size: 10vh;
    }
  }
}

// Desktop
@media 
  only screen and (min-width: 37em) and (orientation: portrait), 
  (min-height: 37em) and (orientation: landscape)
{
  

  .desktop {
    display: inline-block;
  }

  #title {
    position: fixed;
    width: calc(90 * $vw);
    height: calc(40 * $vh);
    left: 0;
    top: 0;
    backdrop-filter: blur(8px);
    background-color: rgba($bg, 0.5);
    margin: calc(5 * $vw);

    span {
      display: inline-block;
      vertical-align: middle;
      line-height: normal;
      padding: 7%;
      padding-top: 4%;

      h1 {
        color: $highlight;
        margin-top: 10px;
        text-align: center;
      }
    }
  }

  .links {
    text-align: left;
    font-size: calc(35vh/6);
    justify-content: space-between;
    display: inline;
    line-height: 1.5;

    #linksDesktopPadding {
      // padding: 2% 5%;
      width: 100%;
      height: 100%;
      background: none;
      overflow: hidden;
      white-space: nowrap;
    }

    button {
      padding: 0;
      font-size: calc(35vh/6);
      display: block;
      transition-duration: 0.5s;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      background: none;
      border: none;
      text-align: left;
      height: 1.4em;
      width: 30vw;
      position: absolute;

      div {
        background: none;
      }

      .inner {
        position: absolute;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        width: 30vw;
        top: 0;
      }

      .linkName.inner {
        left: 3px;
      }

      .linkURL.inner {
        left: calc(-40vw + 3px);
      }

      .outer {
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        border-radius: 0;
      }

      .linkName.outer {
        width: 40vw;
        transition: transform 0.5s forwards, width 0.5s forwards, border-right 0.05s forwards;
      }

      .linkURL.outer {
        left: 40vw;
        zoom: 1;
      }
    }

    button:hover > .outer {
      transform: translateX(-40vw);
      width: 40vw;
      transition-duration: 0.5s;
    }

    button:hover > .linkName.outer {
      border-right: 3px solid $text;
      transform: translateX(calc(-40vw));
      width: 40vw;
    }

    button:hover > .outer > .inner {
      transform: translateX(40vw);
      transition-duration: 0.5s;
    }

  }

  .mobile {
    display: none;
  }
}

#titlescreen {
  position: fixed;
  z-index: 0;
  transform: translateZ(0);
  overflow: hidden;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background: none;
  border-radius: 0;
  #tsparticles {
    height: 100%
  }
}

#waves {
  z-index: 1;
  position: relative;
  top: calc(100vh - $waveheight);
  // left: -8px;
  height: $waveheight;
  width: 100vw;
  background: none;
  border-radius: 0px;
  margin: 0px;
  pointer-events: none;
}

.wave {
  position: absolute;
  bottom: 0px;
  left: 0px;
  animation: waveHeight 1s ease-in 1s forwards;
  animation-play-state: paused;
  animation-delay: calc(#{var(--scrollPercent)} * -1s);
  width: 100vw;
}

@keyframes waveHeight {
  0% {height: 0%;}
  50% {height: 100%;}
  100% {height: 0%;}
}

#bar {
  position: sticky;
  z-index: 5;
  top: 0;
  // margin-left: -8px;
  margin-top: calc(100vh - $waveheight);
  border-radius: 0px;
  background-color: $tertiary;
  height: max-content;
  width: 100vw;
  overflow: hidden;
}

.button {
  background-color: $tertiary;
  color: $text;
  height: max-content;
  padding: 5vh 3vw;
  border: none;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  font-size: min(7vw, 20pt);
  transition-duration: 0.5s;
  float: left;
  text-align: center;
}

.button:hover {
  color: $tertiary;
  background-color: $text;
}

button {
  cursor: pointer;
  user-select: none;
}

.section {
  position: relative;
  top: 0px;
  left: 0px;
  // margin-left: -8px;
  padding-top: 15vh;
  width: 100vw;
  z-index: 2;
  background-color: $bg;
  border-radius: 0px;
  p, h1 {
    z-index: 4;
  }
}

#Resume {
  width: 100%;
  overflow: hidden;
  padding-top: 0.70718232%;
  border: none;

  canvas {
    position: absolute;
    top: 0;
    width: 100vw;
    height: 100%;
    z-index: 2;
  }

  h1 {
    position: relative;
    margin-top: 0;
  }

  #resume-div {  
    position: relative;
    width: 100%;
    padding-top: min(141.40625%, 1165px);
    background: none;
    iframe {
      position: absolute;
      max-width: 824px;
      width: 100%;
      max-height: 1165px;
      height: 100%;
      left: 50%;
      top: 50%;
      bottom: 0;
      transform: translate(-50%, -50%);
      border: none;
      overflow: hidden;
      z-index: 3;
    }
  }
}
